'use strict';
import swiper from 'swiper';
import TimelineMax from "gsap/TimelineMax";
import ScrollMagic from 'scrollmagic';
require('scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap');
require('scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators');
import counter from '../_modules/counter';
var $ = window.jQuery;

$(document).ready(function() {
    var smController = new ScrollMagic.Controller();
    var createAnimationTrigger = function(elem, tween, opt) {
        opt = $.extend({
            triggerElement: elem,
            triggerHook: .75,
            reverse: false
        }, opt);
        // new ScrollMagic.Scene(opt).addTo(smController).addIndicators().setTween(tween);
        new ScrollMagic.Scene(opt).addTo(smController).setTween(tween);
    };

    $(".line").each(function() {
        var elem = $(this).parent(),
            _tm = new TimelineMax;

        _tm.set(this, {
            transformOrigin: 'left center'
        });
        _tm.fromTo(this, .4, {
            scale: 0
        }, {
            scale: 1,
            ease: Expo.easeInOut
        });
        _tm.set(this, {
            transformOrigin: 'right center'
        });
        _tm.to(this, .4, {
            scale: 0,
            ease: Expo.easeInOut
        });
        _tm.set(this, {
            transformOrigin: 'left center'
        });
        _tm.fromTo(this, .5, {
            scale: 0
        }, {
            scale: 1,
            ease: Expo.easeOut,
            delay: .1
        })

        createAnimationTrigger(elem.get(0), _tm, {
            triggerHook: .75
        });
    });

    $(".sec-title, .sec-desc").each(function() {
        var _tm = new TimelineMax;

        _tm.from(this, 1, {
            y: 20,
            opacity: 0,
            ease: Expo.easeOut,
        })

        createAnimationTrigger(this, _tm, {
            triggerHook: 1
        });
    });

    $("[data-parallax-bg]").each(function() {
        var elem = $(this).parent("section"),
            _tm = new TimelineMax;

        _tm.fromTo($(this).find('img').get(0), 1, {
            y: '-42%'
        }, {
            y: '-56%'
        });

        createAnimationTrigger(elem.get(0), _tm, {
            triggerHook: 1,
            duration: '200%',
            reverse: true
        });
    });

    $(".section6, .section7, .section8").each(function() {
        var elem = $(this),
            _tm = new TimelineMax;

        _tm.fromTo($(this).find('.img').get(0), 1, {
            y: '-1%'
        }, {
            y: '-12%'
        });

        _tm.fromTo($(this).find('.cnt').get(0), 1, {
            y: '-1%'
        }, {
            y: '-20%'
        });

        createAnimationTrigger(elem.get(0), _tm, {
            triggerHook: 1,
            duration: '200%',
            reverse: true
        });
    });

    $(".section6 .img, .section7 .img, .section8 .img").each(function() {
        var elem = $(this),
            _tm = new TimelineMax,
            skewAngle = Math.atan(elem.height() / elem.width()) / Math.PI * 180;


        _tm.set(this, {
            transformOrigin: 'left top'
        });
        _tm.fromTo(this, .5, {
            scale: 0
        }, {
            scale: 1,
            ease: Expo.easeInOut
        });
        _tm.set(this, {
            transformOrigin: 'center center'
        });
        _tm.fromTo($(this).find('.clip-mask').get(0), 1.5, {
            x: '-25%',
            y: '-25%',
            skewX: -skewAngle
        }, {
            x: '80%',
            y: '-25%',
            ease: Expo.easeInOut
        });

        createAnimationTrigger(elem.get(0), _tm, {
            triggerHook: 1,
            offset: 100
        });
    });

    $(".container>section").each(function() {
        var elem = $(this);

        new ScrollMagic.Scene({
            triggerElement: this,
            triggerHook: .75
        }).addTo(smController).on("enter", function(e) {
            elem.addClass("sec-visable"),
            e.target.destroy()
        })

        new ScrollMagic.Scene({
            triggerElement: this,
            triggerHook: 1,
            offset: -400
        }).addTo(smController).on("enter", function(e) {
            elem.addClass("sec-preload"),
            e.target.destroy()
        })
    });

    $('.section1 .btn').click(function() {
        layer.open({
            type: 1,
            title: false,
            area: ['1154px', '650px'],
            content: '<iframe height="650" width="1154" src="http://player.youku.com/embed/XMzkxMDYwNjQwNA==" frameborder=0 allowfullscreen></iframe>'
        });
    });

    //-------- sec4 start
    var s4wiper = new swiper('.sec4 .swiper-container', {
        autoplay: {
            delay: 3000
        },
        loop: true,
        effect: 'fade'
    });
    var s4Box = $('.sec4 .switch-box'),
        s4Moving = true,
        s4Index = 1,
        s4Switch = function(index) {
            s4Box.removeClass('switch-box-on-1 switch-box-on-2 switch-box-on-3');
            s4Box.addClass('switch-box-on-' + index);
        };
    setInterval(function () {
        if(!s4Moving) {
            return;
        }
        if(s4Index > 3) {
            s4Index = 1;
        }
        s4Index++;
        s4Switch(s4Index);
    }, 5e3);

    //-------- sec4 end

    //-------- sec21 start
    var s21swiper = new swiper('.section21 .swiper-container', {
        loop: true,
        pagination: {
            el: '.swiper-pagination'
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        }
    });
    //-------- sec21 end

    //-------- counter start
    var kmCounter = $('#km-counter');
    counter.startVal = 120;
    counter.frameVal = 120;
    counter.endVal = 120;
    counter.totalNum = 120;
    counter.start(function(val) {
        kmCounter.text(val);
    });
    $('#km-form select').on('change', function(evt) {
        var elem = evt.target || evt.srcElement;
        console.log(elem.value);
        counter.update(counter.totalNum += 23);
    })
    //-------- counter end
});
