module.exports = (function(){
    var lastTime = 0;
    var vendors = ['webkit', 'moz', 'ms', 'o'];
    for(var x = 0; x < vendors.length && !window.requestAnimationFrame; ++x) {
        window.requestAnimationFrame = window[vendors[x]+'RequestAnimationFrame'];
        window.cancelAnimationFrame =
        window[vendors[x]+'CancelAnimationFrame'] || window[vendors[x]+'CancelRequestAnimationFrame'];
    }
    if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = function(callback, element) {
            var currTime = new Date().getTime();
            var timeToCall = Math.max(0, 16 - (currTime - lastTime));
            var id = window.setTimeout(function() { callback(currTime + timeToCall); },
            timeToCall);
            lastTime = currTime + timeToCall;
            return id;
        };
    }
    if (!window.cancelAnimationFrame) {
        window.cancelAnimationFrame = function(id) {
            clearTimeout(id);
        };
    }

    return {
        totalNum: 0,
        // numArray: [],

        duration: 2000,
        frameVal: 0,
        startVal: 0,
        endVal: 0,
        countDown: false,
        callback: null,
        handler: null,
        printValue: function(val){
            var self = this;

            if (self.handler) self.handler(val);
            self.totalNum = val;
            // self.numArray = num.split('').reverse();
        },
        running: function(timestamp) {
            var self = this;

            if (!self.startTime) self.startTime = timestamp;

            self.timestamp = timestamp;

            var progress = timestamp - self.startTime;
            self.remaining = self.duration - progress;

            if (self.countDown) {
                self.frameVal = self.startVal - self.easeOutExpo(progress, 0, self.startVal - self.endVal, self.duration);
            } else {
                self.frameVal = self.easeOutExpo(progress, self.startVal, self.endVal - self.startVal, self.duration);
            }

            if (self.countDown) {
                self.frameVal = (self.frameVal < self.endVal) ? self.endVal : self.frameVal;
            } else {
                self.frameVal = (self.frameVal > self.endVal) ? self.endVal : self.frameVal;
            }

            self.frameVal = Math.floor(self.frameVal);

            self.printValue(self.frameVal);

            if (progress < self.duration) {
                self.rAF = requestAnimationFrame(function(timestamp){
                    self.running.call(self, timestamp);
                });
            } else {
                if (self.callback) self.callback();
            }
        },
        start: function(handler, callback) {
            var self = this;

            self.handler = handler;
            self.callback = callback;
            // self.rAF = requestAnimationFrame(function(timestamp){
            //         self.running.call(self, timestamp);
            //     });

            return false;
        },
        update: function (newEndVal) {
            var self = this;

            cancelAnimationFrame(self.rAF);
            self.startTime = 0;
            self.startVal = self.frameVal;
            self.endVal = Number(newEndVal);
            self.countDown = (self.startVal > self.endVal);
            self.rAF = requestAnimationFrame(function(timestamp){
                    self.running.call(self, timestamp);
                });
        },
        easeOutExpo: function(t, b, c, d) {
            return c * (-Math.pow(2, -10 * t / d) + 1) * 1024 / 1023 + b;
        }
    }
})()
